












































import Vue, {PropOptions} from 'vue';
import {KernelPart, KernelPartListParameter} from '@roomle/web-sdk/lib/definitions/typings/kernel';
import HeightToggle from '@/configurator/components/utils/HeightToggle.vue';
import {formatPrice, normalizeFloatingNumber} from '@/common/utils/helper';
import {Nullable} from '@/common/utils/types';
import {prepareParametersForUi} from '@/common/utils/parameters';

export default Vue.extend({

  components: {
    HeightToggle,
  },

  props: {
    part: Object as PropOptions<KernelPart>,
  },

  computed: {
    parametersToShow(): KernelPartListParameter[] {
      return prepareParametersForUi(this.part.parameters);
    },
    price(): Nullable<string> {
      return formatPrice(this.part.currencySymbol, this.part.price);
    },
    count(): number {
      return normalizeFloatingNumber(this.part.count);
    },
    noParameters(): boolean {
      return this.parametersToShow.length === 0;
    },
  },

});
